@font-face {
  font-family: bmxy;
  src: url("../fonts/bmxy.ttf");
}
body {
  height: 100%;
}
.hide {
  display: none;
  visibility: hidden;
  display: block;
  width: 100%;
  height: 0;
}
.h564 {
  height: @564px !important;
  background-color: transparent;
}
.h580 {
  height: @580px !important;
  background-color: transparent;
}
.h878 {
  height: @878px !important;
  background-color: transparent;
}

.icon-before {
  display: inline-block;
  line-height: 1;
  position: relative;
  padding-left: @14px;
}
.icon-before::before {
  content: "";
  width: @4px;
  height: @30px;
  background: linear-gradient(to top, rgb(0, 124, 247), rgb(123, 188, 252));
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.df {
  display: flex;
}
.jc-sb {
  justify-content: space-between;
}
.jc-c {
  justify-content: center;
}
.jc-sa {
  justify-content: space-around;
}
.jc-fe {
  justify-content: flex-end;
}
.ai-c {
  align-items: center;
}
.fw-bold {
  font-weight: bold;
}
.fd-c {
  flex-direction: column;
}
.flw-n {
  flex-wrap: nowrap;
}
.flw-w {
  flex-wrap: wrap;
}
.flex-1 {
  flex: 1;
}
.icon {
  display: inline-block;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
  vertical-align: top;
}
input {
  font-size: @32px;
  line-height: 1.4em;
  background-color: transparent;
  border: 0;
  outline: 0;
  border-radius: 0;
  color: #333;
  padding: 0;
  margin: 0;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}
.btn {
  width: 100%;
  height: auto;
  display: block;
  border: 0;
  outline: 0;
  border-radius: 0;
  text-align: center;
  padding: 0;
  margin: 0;
  font-size: @30px;
  color: #fff;
  background-color: #007cf7;
}
.radius {
  border-radius: 1000em;
}
.box-shadow {
  box-shadow: @8px @8px @8px @4px #bfdefd;
}
// 箭头 灰色
.icon-arrow-right-grey-1424 {
  width: @14px !important;
  height: @24px !important;
  background-image: url(../images/icon/icon-arrow-right-grey-1424.png);
}
.icon-bao {
  width: @26px;
  height: @32px;
  margin-right: @10px;
  background-image: url(../images/icon/icon-bao.png);
}
