/*! 添加亲情账号 css */
.add-family-account {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  .input-box {
    background-color: #fff;
    display: block;
    padding: 0 @30px;
    border: none;
    label {
      display: block;
    }
    .label {
      width: 100%;
      display: block;
      font-size: @30px;
      color: #333;
      line-height: 1;
      padding-top: @32px;
    }
    input {
      display: block;
      width: 100%;
      height: @88px;
      border-bottom: 1px solid #ddd;
      font-size: @28px;
      line-height: 1.4;
      background-color: transparent;
    }
    .radio-box {
      padding: @20px 0;
      border-bottom: 1px solid #ddd;
      span {
        width: @166px;
        height: @46px;
        display: block;
        background-color: #f7f8f9;
        color: #999;
        line-height: @46px;
        text-align: center;
        font-size: @24px;
        border-radius: @4px;
      }
    }
    .input-radio {
      display: none;
      width: 0;
      height: 0;
      &:checked {
        ~ span {
          background-color: #007cf7;
          color: #fff;
        }
      }
    }
  }
  .result-box {
    padding-top: @120px;
    text-align: center;
    img {
      width: @202px;
      height: @150px;
      margin: auto;
    }
    p {
      padding-top: @30px;
      font-size: @36px;
      font-weight: 500;
      line-height: 1;
      color: #333;
    }
    span {
      display: inline-block;
      color: #999;
      font-size: @28px;
      line-height: 1;
      padding-top: @30px;
      b {
        color: rgb(0, 124, 247);
      }
    }
  }
  .result.btn-box {
    padding: @80px @60px;
    .btn {
      height: @80px;
      line-height: @60px;
    }
  }
}
