/*! 智能咨询 css */
.znzx {
  padding: 0;
  overflow: hidden;
  height: 100%;
  .top {
    height: @90px;
    background-color: #e9f4fe;
    padding: 0 @32px;
    .icon {
      width: @50px;
      height: @50px;
      border-radius: 50%;
      background-image: url(../images/znzx/avatar.png);
    }
    span {
      padding: 0 @12px;
      color: #333333;
      flex-shrink: @28px;
    }
    .btn {
      width: @86px;
      height: @40px;
      font-size: @24px;
      line-height: @40px;
      font-weight: 500;
      color: #f9fafb;
      border-radius: @4px;
    }
  }
  .center {
    background-color: #faf9fb;
    overflow: auto;
    padding-bottom: @40px;
    .chat {
      padding: @60px @30px 0;
      p {
        max-width: 90%;
        display: inline-block;
        min-height: @80px;
        line-height: @40px;
        border-radius: @4px;
        background-color: #fff;
        padding: @20px;
        font-size: @28px;
        margin-bottom: @30px;
        color: #333;
      }
      .user-question {
        text-align: right;
        p {
          background-color: #dbecfd;
          border-radius: 200em;
          border-top-right-radius: 0;
        }
      }
    }
    .question {
      padding: 0 @30px;
      ul {
        padding: 0 @30px;
        background-color: #fff;
        border-radius: @4px;
        box-shadow: 0 0 @6px @6px #eee;
      }
      li {
        height: @80px;
        line-height: @40px;
        font-size: @28px;
        color: #333;
        border-bottom: 1px solid #ddd;
        &:last-of-type {
          border-bottom: 0;
        }
      }
    }
  }
  .bottom {
    height: @120px;
    background-color: #eee;
    padding: 0 @30px;
    textarea {
      height: @80px;
      border-radius: @6px;
      background-color: #f7f8f9;
      border: 1px solid #ddd;
      margin-right: @20px;
      padding: @10px;
      line-height: 1.4;
      font-size: @32px;
    }
    .btn {
      width: @52px;
      height: @52px;
      background-color: transparent;
      &:first-of-type {
        margin-right: @20px;
      }
    }
    .icon {
      width: @52px;
      height: @52px;
    }
    .icon-smile {
      background-image: url(../images/znzx/icon-smile.png);
    }
    .icon-add {
      background-image: url(../images/znzx/icon-add.png);
    }
  }
}
