/*! 我的账号安全 css */
.wdzhaq {
  padding: 0;
  min-height: 100%;
  background-color: #f7f8f9;
  .link-box {
    padding: 0 @30px;
    background-color: #fff;
    margin-bottom: @20px;
    .link {
      height: @120px;
      border-bottom: 1px solid #ddd;
      &:last-of-type {
        border: 0;
      }
      .icon {
        width: @66px;
        height: @66px;
        margin-right: @20px;
      }
      .icon-phone {
        background-image: url(../images/wdzhaq/icon-phone.png);
      }
      .icon-password {
        background-image: url(../images/wdzhaq/icon-password.png);
      }
      .icon-email {
        background-image: url(../images/wdzhaq/icon-email.png);
      }

      .left {
        font-size: @28px;
        line-height: 1;
        p {
          color: #333;
        }
        span {
          display: inline-block;
          font-size: @24px;
          color: #999;
          padding-top: @12px;
        }
      }
      .right {
        height: 100%;
        .icon {
          margin-right: 0;
        }
      }
      .btn {
        width: @140px;
        height: @44px;
        line-height: @44px;
        background-color: transparent;
        color: #999;
        font-size: @24px;
        border: 1px solid #ddd;
        border-radius: 50em;
        &.active {
          color: #1a90fe;
          border-color: #1a90fe;
        }
      }
    }
  }
}
