/*! 预约详情 css */
.yyxq {
  padding: 0;
  height: 100%;
  padding-top: @15px;
  .card {
    width: @720px;
    height: @316px;
    margin: auto;
    background: url(../images/icon/bg-card-shebao.png) no-repeat right bottom;
    background-size: 100% 100%;
    padding: @15px @50px 0 @40px;
    .top {
      height: @90px;
      border-bottom: 1px dashed #ddd;
      span {
        display: inline-block;
        font-size: @32px;
        color: #333;
        font-weight: 500;
        line-height: 1;
      }
      .btn {
        width: @120px;
        height: @40px;
        background-color: #eafddf;
        border-radius: @4px;
        color: #40b400;
        font-size: @24px;
        line-height: @40px;
      }
    }
    .center {
      padding-top: @4px;
      span {
        font-size: @24px;
        color: #999;
        line-height: @44px;
      }
    }
  }
  .before-line-box {
    margin-top: @20px;
  }
  ul {
    padding: 0 @30px;
  }
  li {
    border-bottom: 1px solid #ddd;
    height: @100px;
    &:first-of-type {
      border-top: 1px solid #ddd;
      margin-top: @10px;
    }
    span {
      font-size: @28px;
      line-height: 1;
      color: #999;
      &:first-of-type {
        color: #333;
      }
    }
  }
  .btn-box {
    padding: @40px @60px;
    .btn {
      height: @80px;
      line-height: @80px;
      font-size: @36px;
    }
  }
}
