/*! 我的社保详情 登陆 注册 忘记密码 css */

.login-registr {
  padding: 0;
  position: relative;
  background-color: #fff;
  .bg-header {
    img {
      height: @328px;
    }
  }
  .input-wrapper {
    width: @690px;
    padding: 0;
    border-radius: @16px;
    height: @540px;
    background-color: #fff;
    box-shadow: 0 0 @12px @12px rgba(13, 101, 245, 0.3);
    margin: auto;
    position: absolute;
    top: @260px;
    left: @30px;
    z-index: 11;
    &.forget {
      height: @476px;
    }
    &.registr {
      height: @756px;
    }
    &.result {
        padding-top: @90px;
        img {
            display: block;
            width: @328px;
            height: @166px;
            margin: 0 auto;
        }
        p {
            font-size: @32px;
            text-align: center;
            color: #333;
            line-height: 1;
            padding-top: @40px;
        }
    }
    .input-box {
      padding: 0 @60px;
      height: @100px;
      margin-top: @20px;
      position: relative;
      border: 0;
      &:first-of-type {
        margin-top: @40px;
      }
      &.checkbox {
        height: @80px;
        line-height: @80px;
        margin-top: 0;
        font-size: @24px;
        label {
          width: 100%;
          height: @80px;
        }
        .input-checkbox {
          display: none;
          &:checked {
            ~ .icon-checkbox {
              background-color: #f0f;
            }
          }
        }
        .icon-checkbox {
          width: @24px;
          height: @24px;
          background-color: #ff0;
          margin-right: @4px;
          position: static;
          transform: translate(0, 0);
        }
      }
      input {
        display: block;
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #e8e8e8;
        padding-left: @58px;
        font-size: @32px;
        line-height: @98px;
        border-radius: 0;
        line-height: 1.4em !important;
      }
      .icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: @64px;
      }
      .verification-code {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: @60px;
        width: @160px;
        height: @60px;
        border-radius: @6px;
        background-color: #eee;
        border: 0;
        .btn {
          width: 100%;
          height: 100%;
          background-color: transparent;
          border-radius: 0;
          border: 0;
          border-radius: @6px;
        }
        &.forget {
          height: @50px;
          background-color: transparent;
          border-radius: 0;
          .btn {
            height: @50px;
            line-height: @50px;
            border-radius: @100px;
            color: #fff;
            font-size: @26px;
            text-align: center;
            background-color: #027df7;
          }
        }
      }
      &.code {
        input {
          padding-right: @170px;
        }
      }
    }
  }
  .btn-box {
    width: 100%;
    position: absolute;
    top: @750px;
    left: 0;
    z-index: 33;
    padding: 0 @65px;
    &.forget {
      top: @680px;
    }
    &.registr {
      top: @960px;
    }
    .btn {
      box-shadow: 0 @8px @12px @12px rgba(13, 101, 245, 0.3);
      border-radius: @100px;
      height: @100px;
      line-height: @100px;
      font-size: @36px;
      &.cancel {
        background-color: #fff;
        border: 1px solid #ddd;
        color: #ddd;
        box-shadow: none;
        margin-top: @30px;
      }
    }
  }
  .help {
    width: 100%;
    text-align: center;
    height: @28px;
    font-size: 0;
    .link {
      display: inline-block;
      font-size: @28px;
      line-height: 1;
      position: relative;
      color: #007cf7;
      &:first-of-type {
        padding-right: @60px;
        &:after {
          content: "";
          position: absolute;
          right: @28px;
          top: 0;
          width: @2px;
          height: @28px;
          background-color: #007cf7;
        }
      }
    }
  }
  .third-login {
    width: 100%;
    padding: @104px @84px @92px;
    .link {
      text-align: center;
    }
    p {
      font-size: @16px;
      line-height: 1;
      padding-top: @10px;
      color: #32acfc;
    }
    .alipay {
      p {
        color: #1198ef;
      }
    }
    .weibo {
      p {
        color: #e60024;
      }
    }
    .wechat {
      p {
        color: #68ba2c;
      }
    }
    .icon {
      width: @66px;
      height: @66px;
    }
  }
  .icon-wechat {
    background-image: url(../images/loginAndRegistration/icon-wechat.png);
  }
  .icon-QQ {
    background-image: url(../images/loginAndRegistration/icon-QQ.png);
  }
  .icon-weibo {
    background-image: url(../images/loginAndRegistration/icon-weibo.png);
  }
  .icon-alipay {
    background-image: url(../images/loginAndRegistration/icon-alipay.png);
  }
  .icon-phone {
    width: @28px;
    height: @40px;
    background-image: url(../images/loginAndRegistration/icon-phone.png);
  }
  .icon-password {
    width: @28px;
    height: @32px;
    background-image: url(../images/loginAndRegistration/icon-password.png);
  }
  .icon-verification-code {
    width: @28px;
    height: @32px;
    background-image: url(../images/loginAndRegistration/icon-ver-code.png);
  }
  .icon-username {
    width: @30px;
    height: @34px;
    background-image: url(../images/loginAndRegistration/icon-username.png);
  }
  .icon-user {
    width: @32px;
    height: @32px;
    background-image: url(../images/loginAndRegistration/icon-user.png);
  }
  .icon-id-card {
    width: @38px;
    height: @26px;
    background-image: url(../images/loginAndRegistration/icon-id-card.png);
  }
}
