/*! 我的签订协议 css */
.before-line-box {
  padding: 0 @30px;
  .before-line {
    color: #333;
    line-height: 1;
    font-size: @30px;
    height: @80px;
    padding-top: @25px;
    span {
      display: inline-block;
      position: relative;
      padding-left: @12px;
      font-weight: 500;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: @4px;
        height: 100%;
        background: linear-gradient(
          to top,
          rgb(0, 124, 247),
          rgb(123, 188, 252)
        );
      }
    }
  }
}

.wdqdxy {
  padding: 0;
  min-height: 100%;
  background-color: #fff;

  .content {
    padding: 0 @30px;
    > div {
      margin-bottom: @20px;
    }
    .img-box {
      width: @210px;
      height: @220px;
      border: 1px solid #ddd;
      border-radius: @6px;
    }
    img {
      display: block;
      width: @195px;
      height: @204px;
      margin: @8px auto;
    }
    p {
      font-size: @24px;
      line-height: 1;
      color: #666;
      padding-top: @20px;
      text-align: center;
    }
  }
}
