/*! 个人详情 css */
.personal-info {
  padding: 0;
  min-height: 100%;
  background-color: hsl(210, 14%, 97%);
  .box {
    margin-bottom: @20px;
    h2 {
      font-weight: 500;
      color: #027df7;
      font-size: @32px;
      height: @70px;
      line-height: @70px;
      padding-left: @30px;
      background-color: #fff;
    }
    ul {
      padding: 0 @30px;
      background-color: #fff;
    }
    li {
      height: @100px;
      border-bottom: 1px solid #ddd;
      &:last-of-type {
        border-bottom: 0;
      }
      span {
        font-size: @28px;
        color: #999;
        &:first-of-type {
          color: #333;
        }
      }
    }
  }
  .btn-box {
    padding: @20px @60px @88px;
  }
}
