/*! 养老保险个人缴费 css */
.ylbxgrjf {
  width: 100%;
  height: 100%;
  background-color: #f7f8f9;
  padding: @80px 0 0;

  .icon-select-down-triangle-gray {
    width: @18px;
    height: @10px;
    background-image: url(../images/ylbxgrjf/icon-arrow-down-triangle-gray.png);
    margin-left: @6px;
  }
  .top-fixed {
    width: 100%;
    background-color: #f7f8f9;
    box-shadow: 0 2px 2px 2px #f7f8f9;
    height: @80px;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999;
    .title {
      padding-left: @30px;
      color: #333;
      padding-top: @24px;
    }
  }
  .content {
    width: 100%;
  }
  ul {
    width: 100%;
    padding: 0 @30px;
  }
  li {
    width: 100%;
    height: @342px;
    background-color: #fff;
    position: relative;
    box-shadow: 0 @8px @10px @4px #ddd;
    color: #999;
    margin-bottom: @30px;
    .top {
      padding: 0 @50px 0 @40px;
      height: @90px;
      line-height: @90px;
      border-bottom: 1px solid #e9e9e9;
      .left {
        color: #333;
      }
      .right {
        color: #999;
      }
    }
    .center {
      padding-left: @40px;
      padding-top: @22px;
      .row {
        &.two {
          padding-top: @26px;
          div {
            margin-right: @20px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
        .key {
          font-size: @26px;
          color: #999;
          line-height: 1;
        }
        .value {
          font-size: @30px;
          font-weight: 500;
          color: #333;
          margin-left: @4px;
          line-height: 1;
        }
        .left {
          margin-right: @100px;
          > div {
            line-height: 1;
            color: #999;
            &:nth-of-type(2) {
              color: #ff9500;
              font-weight: 500;
              font-size: @52px;
              padding-top: @10px;
            }
          }
        }
        .right {
          > div {
            color: #999;
            line-height: 1;
            &:first-of-type {
              padding-top: @6px;
              padding-bottom: @20px;
            }
            span:last-of-type {
              color: #027df7;
            }
          }
        }
      }
    }
    .bottom {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      height: @64px;
      background-color: #e9f3f9;
      border: 1px solid #addefb;
      color: #333;
      opacity: 0.75;
      line-height: @64px;
      padding: 0 @40px;
    }
  }
  .time-select {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 10000;
    .top {
      padding: @50px @30px;
      text-align: center;
      position: relative;
      > div {
        width: @304px;
      }
      .hint {
        display: inline-block;
        width: auto;
        position: absolute;
        line-height: 1;
        bottom: @76px;
        font-size: @30px;
        color: #999;
        left: 50%;
        transform: translateX(-50%);
      }
      input {
        display: block;
        width: 100%;
        border-bottom: 1px solid #eee;
        text-align: center;
        line-height: @76px;
        color: #666;
        font-size: @36px;
        font-family: bmxy;
        &.active {
          border-color: #0880f7;
          color: #0880f7;
        }
      }
      p {
        color: #333;
      }
    }
    .center {
      padding-top: @28px;
      padding-bottom: @64px;
      font-size: @36px;
      text-align: center;
      color: #333;
      .icon {
        width: @22px;
        height: @36px;
      }
      .icon-arrow-right {
        background-image: url(../images/ylbxgrjf/icon-arrow-right-hover.png);
        margin-left: @20px;
        &.disabled {
          background-image: url(../images/ylbxgrjf/icon-arrow-right.png);
        }
      }
      .icon-arrow-left {
        background-image: url(../images/ylbxgrjf/icon-arrow-left-hover.png);
        margin-right: @20px;
        &.disabled {
          background-image: url(../images/ylbxgrjf/icon-arrow-left.png);
        }
      }
    }
    .bottom {
      flex-wrap: wrap;
      width: 100%;
      padding: 0 @30px;
      > div {
        height: @130px;
        width: 25%;
        background-color: #fff;
        border-right: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        &:nth-of-type(4n) {
          border-right: none;
        }
        &:nth-of-type(1) {
          border-top: 1px solid #ddd;
        }
        &:nth-of-type(2) {
          border-top: 1px solid #ddd;
        }
        &:nth-of-type(3) {
          border-top: 1px solid #ddd;
        }
        &:nth-of-type(4) {
          border-top: 1px solid #ddd;
        }
      }
      .btn {
        width: @86px;
        height: @60px;
        color: #333;
        background-color: transparent;
        line-height: @60px;
        border-radius: 0;
        font-size: @36px;
        &.active {
          color: #0880f7;
          background-color: #e4f0fc;
        }
      }
    }
    .btn-box {
      padding: @40px @60px;
      .btn {
        height: @80px;
        line-height: @80px;
        box-shadow: 0 @8px @20px @8px rgba(192, 223, 253, 1);
      }
    }
  }
}
