/*! 我的社保详情 css */
.wdsbxq {
  width: 100%;
  min-height: 100%;
  background-color: #f7f8f9;
  padding: @5px 0;
  ul {
    width: 100%;
  }
  li {
    width: @720px;
    height: @316px;
    background: url(../images/wdsbxq/bg.png) no-repeat bottom right;
    background-size: 100% 100%;
    padding: @15px @64px 0 @56px;
	margin: auto;
    .top {
      height: @90px;
      border-bottom: 1px dashed #e9e9e9;
      .left {
        color: #333;
        font-weight: 500;
      }
      .right {
        span {
          display: block;
          width: @118px;
          height: @40px;
          background-color: #e4f1fe;
          border-radius: @4px;
          color: #0880f7;
          opacity: 0.75;
          line-height: @40px;
          text-align: center;
        }
      }
	}
	.center {
		padding-top: @20px;
		padding-bottom: @32px;
		text-align: center;

		
		p {
			font-size: @26px;
			color: #999;
			opacity: .75;
			line-height: 1;
		}
		span {
			display: inline-block;
			font-size: @28px;
			line-height: 1;
			padding-top: @24px;
			color: #333;
		}
		.num {
			color: #fa2d23;
			font-size: @30px;
			&:after {
				content: '月';
				color: #999;
				font-size: @24px;
			}
		}
	}
	.bottom {
		.left {
			color: #333;
			opacity: .75;
		}
		.right {
			color: #999;
		}
	}
  }
  .icon-bx {
    width: @26px;
    height: @32px;
    background-image: url(../images/wdsbxq/icon-bx.png);
    margin-right: @10px;
  }

}
